body {
  --color-active: #00acdc;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.nav__logo {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.nav {
  height: 100%;
}

.nav__list {
  text-transform: uppercase;
  align-items: center;
  gap: 20px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  list-style-type: none;
  display: flex;
}

.nav__item {
  height: 100%;
}

.nav__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--color-active);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:active, .nav__link:hover {
  color: var(--color-active);
}

